<template>
  <div class="hello">
    <!-- <h1>{{ msg }}</h1> -->
    
    <!-- <h3>Eat eat</h3> -->
    <button class="btn btn-primary" @click="connectKiwrious()">Connect Fork and Spoon</button>
    <br>
    <label class="text-primary">conductivity: {{ conductivity }} uS </label> <br>
    <label class="text-primary">frequency: {{ scalledValue }} Hz </label> <br>
    <label class="text-primary">wave type: {{ oscType }}</label> <br>
    <button class="btn btn-info" style="margin: 5px" @click="changeWaveForm('sine')">Sine</button>
    <button class="btn btn-info" style="margin: 5px" @click="changeWaveForm('triangle')">Triangle</button>
    <button class="btn btn-info" style="margin: 5px" @click="changeWaveForm('square')">Square</button>
    <button class="btn btn-info" style="margin: 5px" @click="changeWaveForm('sawtooth')">Saw Tooth</button>


  </div>
</template>

<script>
import serialService from 'kiwrious-webserial/lib/service/SerialService';
// import {SensorReadResult} from "kiwrious-webserial/lib/data/SensorReadResult";
// import {SensorDecodedValue} from "kiwrious-webserial/lib/data/SensorDecodedValue";

import * as Tone from 'tone';

const asc = new Tone.Oscillator(300, 'sine').toDestination();

export default {
  
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return {
      conductivity:440,
      scalledValue:440,
      oscType:'sine'
    }
  },
  mounted(){
    serialService.onSerialData = (data) => {
      this.conductivity = Math.round(data?.decodedValues[0].value.value);
      this.scalledValue = Math.round(this.scaleFrequency(this.conductivity));
      asc.frequency.setValueAtTime(this.scalledValue);
      console.log(this.conductivity + " " + this.scalledValue);
      //this.$store.commit('setSensorValue', data);
    };
    serialService.onSerialConnection = (connect) => {
      console.log(connect);
      
      asc.start();
      //this.$store.commit('setSensorConnect', connect);
    }
  },
  methods:{
    async connectKiwrious(){
      await serialService.connectAndReadAsync();
    },
    scaleFrequency(frequency) {
      const minFreq = 0;
      const maxFreq = Math.sqrt(60000);
      const newMin = 100;
      const newMax = 14000;
      const sq_frequency = Math.sqrt(frequency);
      const scaledFrequency = (((sq_frequency - minFreq) * (newMax - newMin)) / (maxFreq - minFreq)) + newMin;
      return scaledFrequency;
    },
    changeWaveForm(variation){
      this.oscType = variation;
      asc.type = variation;
    }
  }
}
</script>

